@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;500&display=swap');

.container.one{
    width: 100%;
    color: #04ff00b6;
    /* background-color: #FAF4B7; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    
  
    
}

.container.one .wrapper{
    width: 100%;
    height: 100%;
    border: 1px solid #06FF00;
    padding: 30px;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}
.container.one .content{
    width: 60%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;    
    /* font-weight: 600; */
    /* -webkit-text-stroke: 1px #FFAC41;  */
}

.container.one .content .head{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    height: auto;
    font-weight: 600;
}


.container.one .content .head .text{
    /* width: 140px; */
    height: 130px;
    white-space: nowrap;
    position: relative;
    /* font-family: 'Staatliches', cursive; */
    font-family: 'Source Code Pro', monospace;
    font-size: 80pt; 
    justify-content: flex-start;
    overflow: hidden;
    filter:  url('../assets/noise.svg');
    /* border-right: 2px solid black; */
    

}




.container.one .content .writeup{
    width: 100%;
    position: relative;
    display: flex;
    font-family: 'Source Code Pro', monospace;
    font-size: 15pt; 
    justify-content: flex-start;
    margin-top: 40px;
}



.container.one .image{
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container.one .image img{
    width: 50%;
}





.container.one .content .stack{
    margin-top: 40px;
    font-weight: 600;
    /* visibility: hidden; */
    display: flex;
    justify-content: flex-start;
    width: 100%;
    flex-direction: column;
    font-family: 'Source Code Pro', monospace;
    font-size: 15pt;
    justify-content: space-evenly;
    /* padding-top: 100px; */
    height: 0px;
    overflow: hidden;
}

.container.one .content .stack .line{
    /* padding: 10px 10px 10px 5px; */
    padding: 20px 20px 20px 0px;
}



.container.one .wrapper .background{
    width: 100%;
    height: 100%;
    position: absolute;
    /* display: flex;
    justify-content: center;
    align-items:center ; */
}

.container.one .wrapper .background .byte{
    position: absolute;
    font-size: 100pt;
    font-weight: 400;
    color: transparent;
    -webkit-text-stroke: 1px #04ff00b6;
}